@import "../base/custom-settings";
@import "../base/component-list";

/* -------------------------------------------- \
  single-connection layouts settings
\ --------------------------------------------- */
.single {
  .thumbnail-container {
    height: 40vh;
    overflow: hidden;

    .wp-post-image {
      transform: translateY(-25%);
    }
  }

  .main-container {
    margin-top: 2rem;

    .entry-title {
      font-weight: bold;
    }

    .entry-content {
      > p {
        font-size: 1.2rem;
        line-height: 2;
      }
    }

    .entry-meta {
      .author-card {

      }

      .tag-group {

      }
    }
  }
}
